a {
  text-decoration: none;
}

a:hover,
a:focus,
a:active {
  border: 0;
  text-decoration: none;
}

// Correct issues with buttons
button,
.button {
  text-shadow: none;
  background-image: none;
}

.button:hover,
.button:focus,
.button:active {
  color: white;
  text-shadow: none;
  background-image: none;
}
